<template>
	<div>
		<div 
			v-if="isLoading"
			id="loader"
		>
			<vue-element-loading 
				:active="isLoading"
				:background-color="'rgba(255, 255, 255, 1)'"
				:color="'blue'"
			/>
		</div>
		<div
			v-else
			id="home-page"
		>
			<header class="header">
				<div class="container">
					<navigation-vue
						ref="navigation"	
					/>
				</div>
				<div class="header__separator" />
			</header>
			<router-view />
		</div>
	</div>
</template>

<script>
import { setCookie, getCookie } from '@/helpers/cookies.js';
import NavigationVue from '@/components/TheNavigation.vue';

export default {
	name: 'Home',
	components: {
		'navigation-vue': NavigationVue,
	},
	data() {
		return {
			isLoading: false
		};
	},
	created() {
		const isLoggedIn = getCookie('user_state');
		if (isLoggedIn !== 'logged_in') { 
			setCookie('_token', '', -1);
			setCookie('user_state', '', -1);
			this.$router.push({ name: 'Login', query: {} });
		}
	},
};
</script>

<style lang="scss" scoped>
	#loader {
		width: 100%;
		height: 100vh;
	}
	#home-page {
		height: 100%;
		min-height: 100vh;
	}
	.header {
		width: 100%;
		height: 80px;
		&__separator {
			width: 100%;
			height: 0px;

			border: 1px solid rgba(255, 255, 255, 0.13);
		};
	}
</style>