<template>
	<div class="nav">
		<img
			class="nav__logo"
			src="@/assets/header/logo.png" 
			alt="Logo"
		>
		<nav class="nav__menu">
			<ul class="nav__links">
				<li v-if="role === 'user'">
					<button-vue
						:active="activeDashboard === 'templates'"
						@click="switchDashboard('templates')"
					>
						Шаблоны
					</button-vue>
				</li>
				<li>
					<button-vue
						:active="activeDashboard === 'reports'"
						@click="switchDashboard('reports')"
					>
						Отчеты
					</button-vue>
				</li>
				<li v-if="role === 'admin'">
					<button-vue
						:active="activeDashboard === 'clients' || activeDashboard === 'user'"
						@click="switchDashboard('clients')"
					>
						Клиенты
					</button-vue>
				</li>
			</ul>
			<button-vue
				@click="logout()"
			>
				Выйти из системы
			</button-vue>
		</nav>
	</div>
</template>

<script>
import ButtonVue from '@/components/BaseButton.vue';
import { mapGetters } from 'vuex';
import { setCookie } from '@/helpers/cookies';
export default {
	components: {
		ButtonVue
	},
	computed: {
		...mapGetters(['getActiveDashboard', 'getMyRole']),
		activeDashboard() {
			return this.getActiveDashboard;
		},
		role() {
			return this.getMyRole;
		}
	},
	methods: {
		switchDashboard(route) {
			this.$store.commit('setActiveNestedDashboard', '');
			this.$store.commit('setActiveDashboard', route);
			this.$router.push({ name: route});
		},
		logout() {
			this.$router.push({ name: 'Login', query: {} });
			setCookie('_token', '');
			setCookie('user_state', '');
			this.$store.commit('resetStore');
		}
	}
};
</script>

<style lang="scss" scoped>
	.nav {
		&__menu {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}
		display: flex;
		align-items: center;
		height: 78px;
		&__logo {
			width: 4.2rem;
			height: 3rem;
		}
		&__links {
			display: flex;
			list-style-type: none;
		}
	}
</style>